import './Footer.scss'
import QiwiLogo from '../../assets/images/qiwi-footer-logo.svg'
import PlayMarket from '../../assets/images/PlayMarket.png'
import AppStore from '../../assets/images/AppStore.png'

const FooterSmall = () => {
    return (
        <footer className="footer">
            <div className="container">
                <div className="box">
                    <img src={QiwiLogo} alt="Logo" />
                    <h3>Продай, найди, купи <br/>
                все что ты пожелаешь </h3>
                </div>
                <div className="box">
                    <p>Веб сайт бесплатных обьявлений | на базе
                    имеется 234 944 999 шт обяления</p>
                </div>
                <div className="box">
                    <a href="#">
                        <img src={AppStore} alt="" />
                    </a>
                    <a href="#">
                        <img src={PlayMarket} alt="" />
                    </a>
                </div>
            </div>
        </footer>
    )
}

export default FooterSmall